import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
bridge.send("VKWebAppInit");
import util from './panels/sp';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import More from './panels/More';
import Work from './panels/Work';
import Composition from './panels/Composition';
import Faq from './panels/Faq';

import Schedule from './panels/Schedule/Schedule';
import ScheduleLesson from './panels/Schedule/Schedule-lesson';
import ScheduleTimes from './panels/Schedule/Schedule-times';

import { 
	AdaptivityProvider, 
	AppRoot, 
	Root,
	CardGrid,
	Link,
	useAdaptivity,
	Spinner, 
	Tabbar, 
	TabbarItem, 
	ConfigProvider,
	Group,
	Banner,
	Footer,
	Panel,
	ViewWidth,
	View,
	PanelHeader,
	SplitLayout,
	SplitCol,
	SimpleCell,
	Spacing,
	FixedLayout,
	Search,
	ContentCard,
	Placeholder,
} from '@vkontakte/vkui';

import {
	Icon28HomeOutline,
	Icon28BillheadOutline,
	Icon28Menu,
	Icon24Users,
	Icon24Share,
	Icon28QuestionOutline,
	Icon24Newsfeed,
	Icon28SmartphoneStarsOutline,
    Icon16SadFaceOutline,
	Icon28LogoVkColor,
} from '@vkontakte/icons';

import './style.css'

const App = () => {
	const { viewWidth } = useAdaptivity();

	const [activePanel, setActivePanel] = useState('home');
	const [history, setHistory] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<Spinner/>); 
	// <Spinner />

	const [scheme, SetStateScheme] = useState('bright_light');
	const lights = ['bright_light', 'client_light'];

	function camelCase( scheme, needChange = false ) {
		let isLight = lights.includes( scheme );

		if ( needChange ) {
			isLight = !isLight;
		}

		SetStateScheme( isLight ? 'bright_light' : 'space_gray' );

		bridge.send('VKWebAppSetViewSettings', {
			'status_bar_style': isLight ? 'dark' : 'light',
			'action_bar_color': isLight ? '#FFF' : '#000'
		});
	}

	function goTopPage(name) {
		window.history.pushState( {panel: name}, name );
		setActivePanel(name);
		history.push(name);
	}

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				camelCase( data.scheme );
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		
		window.addEventListener('popstate', () => goBack());

		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const goBack = () => {
		if ( history.length === 1 ) {
			bridge.send("VKWebAppClose", {"status": "success"});
		} else if ( history.length > 1 ) {
			history.pop();
			setActivePanel( history[history.length - 1] );
		}
	}

	return (
		<ConfigProvider isWebView={true} scheme={scheme}>
			<AdaptivityProvider>
				<AppRoot>
					<SplitLayout header={<PanelHeader separator={true} />}>
        				<SplitCol spaced={viewWidth > ViewWidth.MOBILE}>

							<View activePanel={activePanel} history={history} onSwipeBack={goBack} popout={popout}>
								<Home id='home' fetchedUser={fetchedUser} go={go} />
								<Work id='work' go={go} />

								<Panel style={{"background-color": '#FF4E4E'}} id='error' go={go} fetchedUser={fetchedUser}>

								</Panel>

								<Panel id='more'>
									<PanelHeader>Ещё</PanelHeader>
				
									<Group>
										<Banner
											header="Состав"
											subheader=""
											asideMode="expand"
											before={<Icon24Users fill="#3883A8" />}
											onClick={go} data-to="composition"
										/>

										<Banner
											header="Поделиться с друзьями"
											subheader=""
											asideMode="expand"
											before={<Icon24Share fill="#3883A8" />}
											onClick={() => bridge.send("VKWebAppShare", {"link": "https://vk.com/app7820245"})} 
										/>

										<Banner
											header="FAQ - Ответы на более популярные вопросы"
											subheader=""
											asideMode="expand"
											before={<Icon28QuestionOutline width={24} height={24} fill="#3883A8" />}
											onClick={go} data-to="faq"
										/>

										<Banner 
											header="Расписание"
											subheader=""
											asideMode="expand"
											before={<Icon24Newsfeed fill="#3883A8" />}
											onClick={go} data-to="schedule"
										/>

										<Banner 
											header="Сменить тему"
											
											asideMode="expand"
											before={<Icon28SmartphoneStarsOutline fill="#3883A8" />}
											onClick={() => camelCase( scheme, true )}
										/>

									</Group>

									<Footer>Версия программы: 1.3.3</Footer>
								</Panel>

								<Composition id='composition' goTopPage={goTopPage} />
								<Faq id="faq" go={go} />
								<Schedule id="schedule" go={go} />
								<ScheduleLesson id="schedule-lesson" go={go} />
								<ScheduleTimes id="schedule-times" go={go} />
							</View>

							<Tabbar>
								<TabbarItem selected={activePanel === 'home'} onClick={go} data-to="home" text="Главная">
									<Icon28HomeOutline />
								</TabbarItem>

								<TabbarItem selected={activePanel === 'work'} onClick={go} data-to="work" text="ДЗ">
									<Icon28BillheadOutline />
								</TabbarItem>
								
								<TabbarItem selected={activePanel === 'more' || activePanel === 'schedule' || activePanel === 'schedule-lesson' || activePanel === 'schedule-times' || activePanel === 'faq' || activePanel === 'composition'} onClick={go} data-to="more" text="Ещё">
									<Icon28Menu />
								</TabbarItem>
							</Tabbar>
						</SplitCol>
      				</SplitLayout>
				</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>
	);
}

